nav {
    width: 100vw;
    background-color: var(--accentColor);
}

nav ul {
    justify-content: flex-end;
    padding-right: 20px;
/*    padding-bottom: 0px;*/
    font-feature-settings: "c2sc";
    font-variant: small-caps;
/*    align-items: flex-end;*/
}

.socialLinks,nav ul {
    display: flex;
    flex-direction: row;
    list-style-type: none;
}

.socialLinks {
    justify-content: flex-start;
    padding-left: 20px;
}

.socialLinks a {
    border-radius: 0;
    padding: 5px
}

.socialLinks a:hover {
    background-color: var(--accentColorDark);
    color: var(--darkText);
}

nav li a:hover {
    background-color: var(--accentColorDark);
    color: var(--darkText);
}

nav a {
    height: 50px;
    display: flex;
    align-items: center;
    font-weight: 700;
    padding: 5px 10px;
    color: var(--lightText);
    border-radius: 5px 5px 0 0;
    margin: 0 2px;
}

#logo {
    color: var(--lightText);
    font-size: 2.5em;
}

#hamburgerNav{
    display: flex;
    flex-direction: column;
}

#hamburgerButton{
    color: var(--lightText);
    font-size: 2em;
    border: none;
    margin-right: calc(1 * var(--standardMargin));
    background-color: var(--accentColor);    
    padding: var(--standardMargin);
    align-self: flex-end;
}

#hamburgerButton:hover{
    background-color: var(--accentColorDark);
    color: var(--darkText);
    cursor: pointer;   
}

#hamburgerLinks a:hover{  
    background-color: var(--accentColorDark);
    color: var(--darkText);
}