/* CSS variables for the whole site */
:root{
  --accentColor:#6495ED;
  --accentColorLight:#96BAFA;
/*  --accentColorMedium:#4581ED;*/
  --accentColorDark:#4581ED;

  --darkText:#000;
  --lightText:#FFF;

  --standardMargin:20px;
  --smallMargin:5px;
}

body{
  background-color:#fff;
  color: var(--darkText);
  font-family: sans-serif;
  margin:0px;
/*    0px 30px var(--standardMargin);*/
}

a{
  text-decoration: none;
  color: var(--darkText);
}

p a{
  text-decoration: underline;
  color: var(--accentColorDark);
}

.twoColumn{
  display: flex;
  flex:2;
  flex-flow:row wrap;
  justify-content: flex-start;
}


.spreadOut{
  display: flex;
  justify-content: space-between;
}

.verticleCentered{
  display: flex;
  align-content: center;
}


.project{
  /* flex:1;  */
  /* flex: 50%; */
  width:200px;
  background-color: var(--accentColorLight);
  border-radius: 5px;
  /*justify-content: center; */
  padding:10px;
  margin:10px;
}

.project img{
  width:200px;
}

.content{
    margin: 10px;
}

#gameLinks a:hover {
    color: var(--accentColorDark);
    text-decoration: underline;
}
#gameLinks{
    font-weight: bold;
}

/*Screens in landscape mode for mobile*/
@media only screen and (orientation: landscape) {
  .mobile {
      display: none;
  }
}
/*Screens in portrait mode for desktop*/
@media only screen and (orientation: portrait) {
  .desktop {
      display: none;
  }
}

